	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter - Forty by HTML5 UP","author":"Hunter Chang"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/side-projects/index.md absPath of file >>> MarkdownRemark","html":"<p>Mixing a full-time job programming and hobby-time job blogging is a tricky balancing act. To the extent where I could give up on the blogging side of things for weeks, months or even years and not feel too bad about it. After all, we’re not in this for money, we’re in it for the kudos and warm fuzzy feeling we get from passively helping out other geeks.</p>\n<p>However this presentation, and the conversations it prompted with work colleagues, has convinced me to take up blogging again. Hopefully it will inspire you in much the same way!</p>\n<script async class=\"speakerdeck-embed\" data-id=\"508b4413c21140000200a7b7\" data-ratio=\"1.33333333333333\" src=\"//speakerdeck.com/assets/embed.js\"></script>","frontmatter":{"title":"Side Projects","date":"November 23, 2013","tags":["presentations-2"]}}},"pathContext":{}}

